import BaseIconSlider from "@PegasusCore/pegasus-one-icon-carousel/src/script";
import Flickity from "flickity";

class IconSlider {
    isPreview = false;

    tickerSpeed = 1;

    slides = [];

    constructor(isPreview) {
        this.isPreview = isPreview;
    }

    init() {
        this.sliderContainer = this.$refs.iconSlider;
        this.slides = [...this.sliderContainer.children];

        if (!this.isPreview) {
            this.initSlider();
        } 
    }

    /**
     * initSlider
     * @return {void}
     */
    initSlider() {
        const slider = this.sliderContainer;
        if (!slider) return;

        console.log("initSlider");
        console.log(slider);

        let speed = 0.5; // Adjust speed here
        let isPaused = false;
        let duplicateContent = false;

        this.cloneSlides();
    

        let sliderWidth = slider.scrollWidth / (duplicateContent ? 2 : 1);
        let position = 0;

        function update() {
            if (!isPaused) {
                position -= speed;
                if (Math.abs(position) >= sliderWidth) {
                    position = 0;
                }
                slider.style.transform = `translateX(${position}px)`;
            }
            requestAnimationFrame(update);
        }

        // Pause scrolling when interacting
        slider.addEventListener("mouseenter", () => (isPaused = true));
        slider.addEventListener("mouseleave", () => (isPaused = false));

        // Adjust on window resize
        window.addEventListener("resize", () => {
            sliderWidth = slider.scrollWidth / (duplicateContent ? 2 : 1);
        });

        slider.style.display = "flex";
        slider.style.whiteSpace = "nowrap";
        slider.style.willChange = "transform";
        
        update();

    }

    cloneSlides() {
        const firstClones = this.slides.slice(0, this.slidesInView).map((slide) => slide.cloneNode(true));
        const lastClones = this.slides.slice(-this.slidesInView).map((slide) => slide.cloneNode(true));

        firstClones.forEach((clone) => this.sliderContainer.appendChild(clone));
        lastClones.forEach((clone) =>
            this.sliderContainer.insertBefore(clone, this.slides[0])
        );
    }
}

document.addEventListener("alpine:init", () => {
    Alpine.data("iconSlider", (isPreview) => new IconSlider(isPreview));
});