import Flickity from "flickity";
import pegasus from "../../assets/scripts/global/01-pegasus";

export default class ImageChangeTextSlider {
    image = "";

    /**
     * @var {array}
     */
    slides = null;

    /**
     * @var {HTMLElement}
     */
    slider = null;

    /**
     * @var {HTMLElement}
     */
    sliderContainer = null;

    constructor(image) {
        this.image = image;
    }

    init() {
        if (this.slider !== null) {
            this.slider.destroy();
        }

        this.sliderContainer = this.$root.querySelector(
            ".ImageChangeTextSlider__slider"
        );
        this.slides = this.sliderContainer.querySelectorAll(".slide");

        this.initSlider();

        pegasus.onResizeFinished(() => {
            this.initSlider();
        }, true);
        
    }

    initSlider() {
        if (this.slider !== null) {
            this.slider.destroy();
        }

        let drag = false;

        if(window.outerWidth < 768) {   
            drag = true;
        }

        console.log('drag', drag);

        let options = {
            cellAlign: "left",
            contain: true,
            wrapAround: false,
            prevNextButtons: false,
            pageDots: false,
            draggable: drag,
            percentPosition: false,
            freeScroll: false,
            on: {
                change: (i) => {
                    this.slider.selectedElement.click();
                },
            },
        };

        setTimeout(() => {
            this.slider = new Flickity(this.sliderContainer, options);
        }, 200);
    }
}

document.addEventListener("alpine:init", () => {
    Alpine.data(
        "imageChangeTextSlider",
        (image) => new ImageChangeTextSlider(image)
    );
});
