class PegasusLazyLoader {
    constructor() {
        this.images = [];
        this.loadingImages = new Set();
        this.connection = navigator.connection || navigator.mozConnection || navigator.webkitConnection;
        this.defaultOptions = {
            root: null,
            rootMargin: '10px',
            threshold: [0, 0.1, 0.2]
        };
        
        // Initialize when browser is idle
        this.init = this.init.bind(this);
        if ('requestIdleCallback' in window) {
            requestIdleCallback(this.init);
        } else {
            setTimeout(this.init, 1);
        }
    }

    init() {
        // Get all images but don't hold references unnecessarily
        const images = document.querySelectorAll('img[data-src]');
        this.images = Array.from(images)
            .map(img => ({
                element: img,
                priority: this.calculatePriority(img)
            }))
            .sort((a, b) => b.priority - a.priority); // Sort by priority
    
        this.setupObserver();
        this.setupConnectionWatcher();
    }

    calculatePriority(img) {
        const rect = img.getBoundingClientRect();
        const viewportDistance = rect.top - window.innerHeight;
        const isVisible = viewportDistance < window.innerHeight;
        return isVisible ? 1 : (1 / Math.max(1, viewportDistance));
    }

    setupObserver() {
        this.observer = new IntersectionObserver(entries => {
            entries.forEach(entry => {
                if (entry.isIntersecting && !this.loadingImages.has(entry.target)) {
                    this.queueImage(entry.target);
                }
            });
        }, this.defaultOptions);

        this.images.forEach(({ element }) => this.observer.observe(element));
    }

    setupConnectionWatcher() {
        if (this.connection) {
            this.connection.addEventListener('change', () => {
                this.updateLoadingStrategy();
            });
        }
    }

    updateLoadingStrategy() {
        if (!this.connection) return;
        
        // Adjust loading behavior based on connection (i.e. for slower add more margin)
        if (this.connection.saveData) {
            this.defaultOptions.rootMargin = '0px';
        } else if (this.connection.effectiveType === '4g') {
            this.defaultOptions.rootMargin = '50px';
        }
    }

    queueImage(img) {
        if (this.loadingImages.has(img)) return; // Already loaded
        
        this.loadingImages.add(img);
        this.loadImage(img).finally(() => {
            this.loadingImages.delete(img);
            this.observer.unobserve(img);
        });
    }

    async loadImage(img) {
        const wrapper = img.closest('.pegasus-lazy-wrapper');
        const picture = img.closest('picture');

        try {
            // Create and configure temp image
            const tempImage = new Image();
            const loadPromise = new Promise((resolve, reject) => {
                tempImage.onload = resolve;
                tempImage.onerror = reject;
            });

            // Set attributes
            if (img.dataset.srcset) tempImage.srcset = img.dataset.srcset;
            if (img.dataset.sizes) tempImage.sizes = img.dataset.sizes;
            tempImage.src = img.dataset.src;

            // Handle picture sources
            if (picture) {
                const sources = picture.querySelectorAll('source');
                sources.forEach(source => {
                    if (source.dataset.srcset) {
                        source.srcset = source.dataset.srcset;
                    }
                });
            }

            // Wait for decode and load (waiting for decode is important so images dont look like they are loading from the 90's)
            await Promise.all([
                loadPromise,
                tempImage.decode()
            ]);

            // Update actual image
            if (img.dataset.srcset) img.srcset = img.dataset.srcset;
            if (img.dataset.sizes) img.sizes = img.dataset.sizes;
            img.src = img.dataset.src;

            // Clean up data attributes
            delete img.dataset.src;
            delete img.dataset.srcset;
            delete img.dataset.sizes;

            if (wrapper) wrapper.classList.add('is-loaded');

        } catch (error) {
            console.warn('Image loading failed:', error);
            // Fallback loading
            if (img.dataset.srcset) img.srcset = img.dataset.srcset;
            if (img.dataset.sizes) img.sizes = img.dataset.sizes;
            img.src = img.dataset.src;
            
            const loadHandler = () => {
                if (wrapper) wrapper.classList.add('is-loaded');
                img.removeEventListener('load', loadHandler);
            };
            img.addEventListener('load', loadHandler);
        }
    }

    disconnect() {
        if (this.observer) {
            this.observer.disconnect();
        }
        this.images = [];
        this.loadingImages.clear();
    }
}

// Initialize when the DOM is ready
if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', () => new PegasusLazyLoader());
} else {
    new PegasusLazyLoader();
}
