//Add mutation observer to check that all the all the expanding images have a wrapper class witht the class of .pegasus-lazy-wrapper.is-loaded
class ExpandingImages {
    lazyImages = [];

    checkAllImagesLoaded() {
        const allLoaded = Array.from(this.imagesLazy).every(wrapper => 
            wrapper.classList.contains('is-loaded')
        );
        
        if (allLoaded) {
            this.$el.classList.add('is-loaded');
        }
    }

    init() {
        this.imagesLazy = this.$el.querySelectorAll('.pegasus-lazy-wrapper');
        // If there are no lazy images, mark the component as loaded
        if (this.imagesLazy.length === 0) {
            this.$el.classList.add('is-loaded');
            return;
        }

        const observer = new MutationObserver((mutations) => {
            mutations.forEach((mutation) => {
                if (mutation.type === 'attributes' && 
                    mutation.attributeName === 'class' &&
                    mutation.target.classList.contains('pegasus-lazy-wrapper')) {
                    this.checkAllImagesLoaded();
                }
            });
        });

        this.imagesLazy.forEach(wrapper => {
            observer.observe(wrapper, {
                attributes: true,
                attributeFilter: ['class']
            });
        });
    }
}

document.addEventListener("alpine:init", () => {
    Alpine.data(
        "expandingImages", () => new ExpandingImages()
    );
});
