export default class ParacelsusApp {
    atTop = true;
    nearTop = true;
    goingUp = false;
    activeMenuItem = false;
    langSwitcherOpen = false;
    stationaryTimeout = null;
    pageSwitcherName = false;
    pageSwitcherLabel = false;

    init() {
        var prevScrollTop = 0,
            paracelsus = this;

        window.addEventListener('scroll', function () {
            const scrollTop = document.documentElement.scrollTop;
            if (paracelsus.goingUp && scrollTop > prevScrollTop) {
              paracelsus.goingUp = false;
            } else if (!paracelsus.goingUp && scrollTop < prevScrollTop) {
                paracelsus.goingUp = true;
            }
            prevScrollTop = scrollTop < 0 ? 0 : scrollTop;

            Alpine.store('stationaryScroll', false);

            clearTimeout(this.stationaryTimeout);
            this.stationaryTimeout = setTimeout(() => {
                Alpine.store('stationaryScroll', true);
            }, 5000);
          }, false);
    }
}

document.addEventListener('alpine:init', () => {
    Alpine.data('paracelsusApp', () => new ParacelsusApp());
});